import React, { FC } from "react";
import { PaginationComponentProps } from "react-data-table-component/dist/src/DataTable/types";

import { ButtonGroup, ButtonGroupButtonProps } from "../ButtonGroup";
import { AlignPosition, SidePosition } from "../DropDown";
import { colors, Margins, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";

import RowsPerPageDropdown from "./RowsPerPageDropdown/RowsPerPageDropdown";
import { PaginationContentWrapper, StyledPagination } from "./Pagination.styles";

export interface PaginationCustomProps extends Margins, PaginationComponentProps {
	dataQA: string
	ariaLabel: string
	side?: SidePosition
	align?: AlignPosition
	className?: string
	rowOptions?: Array<{ value: number, label: string }>
}

const defaultRowOptions = [
	{ value: 20, label: "20" },
	{ value: 50, label: "50" },
	{ value: 100, label: "100" }];

const Pagination: FC<PaginationCustomProps> = ({
	rowOptions = defaultRowOptions,
	rowsPerPage = 20,
	rowCount,
	onChangePage,
	onChangeRowsPerPage,
	currentPage,
	side,
	align,
	dataQA,
	ariaLabel,
	className,
	m,
	ml,
	mr,
	mt,
	mb,
}) => {
	const firstShownRow = currentPage === 1 ? 1 : (currentPage - 1) * rowsPerPage + 1;
	const lastShownRow = currentPage * rowsPerPage > rowCount ? rowCount : currentPage * rowsPerPage;

	const handlePreviousButtonClick = () => {
		onChangePage(currentPage - 1, rowCount);
	};

	const handleNextButtonClick = () => {
		onChangePage(currentPage + 1, rowCount);
	};

	const buttons: ButtonGroupButtonProps[] = [
		{
			onClick: handlePreviousButtonClick,
			buttonText: "Previous",
			leftIcon: "ArrowLeft",
			dataQA: `${dataQA}-previous-button`,
			ariaLabel: "Previous Page",
			$isDisabled: currentPage === 1,
		},
		{
			onClick: handleNextButtonClick,
			buttonText: "Next",
			rightIcon: "ArrowRight",
			dataQA: `${dataQA}-next-button`,
			ariaLabel: "Next Page",
			$isDisabled: currentPage === Math.ceil(rowCount / rowsPerPage),
		},
	];

	return (
		<StyledPagination
			data-qa={`${dataQA}-pagination`}
			className={className}
			m={m}
			ml={ml}
			mr={mr}
			mt={mt}
			mb={mb}
		>
			{
				rowOptions.length > 0 ?
					(
						<PaginationContentWrapper>
							<Typography
								variant="bodyCopySmall"
								component="span"
								dataQA="items-per-page"
								color={`${colors.black}A8`}
								m={`0 ${spacingMap.xxs} 0 0`}
							>
								Items per page
							</Typography>
							<RowsPerPageDropdown
								rowOptions={rowOptions}
								rowsPerPage={rowsPerPage}
								dataQA={dataQA}
								side={side}
								align={align}
								ariaLabel={ariaLabel}
								onChangeRowsPerPage={onChangeRowsPerPage}
								currentPage={currentPage}
							/>
						</PaginationContentWrapper>
					) :
					null
			}
			<PaginationContentWrapper>
				<Typography
					variant="bodyCopySmall"
					component="span"
					dataQA="items-shown"
					color={`${colors.black}A8`}
					aria-label={`Showing rows ${firstShownRow} to ${lastShownRow} of ${rowCount} total rows `}
					m={`0 ${spacingMap.xs} 0 0`}
				>
					Showing {firstShownRow} - {lastShownRow} of {rowCount}
				</Typography>
				<ButtonGroup
					buttonSize="small"
					buttons={buttons}
					dataQA="pagination-buttons"
					ariaLabel="pagination buttons"
				/>
			</PaginationContentWrapper>
		</StyledPagination>
	);
};

Pagination.displayName = "Pagination";
export default Pagination;
